import Vue from 'vue'
import Router from 'vue-router'


import Index from '../components/index/index'

import Particulars from '../components/index/particulars'





Vue.use(Router)

export default new Router({
	routes: [
	
		{
			path: '/',
			name: 'index',
			component: Index,
			// hidden: true,
			meta: {
				title: '首页',
				requireAuth: true
			}
		},
		{
			path: '/index',
			name: 'index',
			component: Index,
			// hidden: true,
			meta: {
				title: '首页',
				requireAuth: true
			}
		},
		{
			path: '/particulars',
			name: 'particulars',
			component: Particulars,
			// hidden: true,
			meta: {
				title: '商品详情',
				requireAuth: true
			}
		},

		
	]
})



