<template>
    <div class="index">
        <div v-wechat-title='$route.meta.title'></div>
        <div class="index_box">
            <!-- <ul class="index_ul">
                <li class="index_img">
                    <img src="../../assets/1.jpg" alt="">
                </li>
                <li class="index_text">
                    <p class="index_name">套餐名称可以很长，像这样这样</p>
                    <p class="index_introduce">针对敏感肌肤护理（原价1924元，秒杀价：900元）
                        包含单品：
                        ①核心修复成分（次抛脱敏因子）：脱敏因子精华！适合任何敏感肌肤，泛红 干燥 瘙痒 疹子等，促进细胞再生，修护受损组织，抑制组胺生成，避免敏感复发！</p>
                    <p class="index_price">￥170 <span class="original">原价<s>￥555</s></span></p>
                </li>
            </ul> -->
            <ul class="index_ul" v-for="(item,index) in ovtrn" @click="particulars(index)">
                <li class="index_img">
                    <img :src="item.img_url" alt="">
                </li>
                <li class="index_text">
                    <p class="index_name">{{item.set_meal_name}}</p>
                    <p class="index_introduce">{{item.set_meal_effect}}</p>
                    <p class="index_price">￥{{item.set_meal_price}}&nbsp;&nbsp;<span
                            class="original">原价<s>￥{{item.set_meal_originalprice}}</s></span></p>
                </li>
            </ul>

        </div>
    </div>
</template>
<script>
    import data from '../../../static/indexovr.json'
    export default {
        name: 'index',
        data() {
            return {
                dataumkk: [],
                ovtrn: []


            }
        },
        mounted() {
            this.ovtrn = data.ovtrn;

        },
        methods: {
            particulars(index) {
                this.$router.push({path: "/particulars", query: {id: index}})
                // console.log(index)
                // console.log(this.ovtrn[index])

            }
        },

        // beforeCreate: function () {
        //     this.$axios.get("./static/indexovr.json", {}).then((res) => {
        //         console.log(res)
        //         this.dataumkk = res.data.ovtrn
        //         console.log(this.dataumkk)
        //     })
        // },

    }
</script>
<style>
    .index_box {
        width: 100%;
        min-height: 120px;
        /* background: #eeeeee; */
        padding: 10px;
        box-sizing: border-box;
    }

    .index_ul {
        width: 100%;
        height: 120px;
        background: #ffffff;
        margin-bottom: 10px;
        border: 1px solid #cacaca;
    }

    .index_ul li {
        float: left;
    }

    .index_img {
        width: 120px;
        height: 120px;
        /* background: green; */
        border-radius: 10px;
    }

    .index_img img {
        width: 100%;
        height: 100%;
    }

    .index_text {
        width: 225px;
        height: 120px;
        /* background: yellow; */
        padding: 10px 5px;
        box-sizing: border-box;
        margin-left: 10px;
        position: relative;
        /* border-radius: 10px; */

    }

    .index_name {
        width: 100%;
        height: 20px;
        line-height: 20px;
        /* background: red; */
        font-weight: 600;
        color: red;
        /* 超出文字 */
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }

    .index_introduce {
        width: 100%;
        max-height: 60px;
        /* background: green; */
        line-height: 20px;
        letter-spacing: 1.5px;
        font-size: 10px;
        font-weight: 900;
        /* 超出文字 */
        word-break: break-all;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 3;
        overflow: hidden;

    }

    .index_price {
        position: absolute;
        width: 214px;
        bottom: 0;
        height: 20px;
        color: red;
        font-weight: 900;
        font-size: 16px;
        /* background: blue; */
        box-sizing: border-box;
        line-height: 20px;
        /* 超出文字 */
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;

    }

    .original {
        font-size: 10px;
    }
</style>


