<template>
    <div class="particulars">
        <div v-wechat-title='$route.meta.title'></div>
        <div class="parti">
            <ul class="parti_ul">
                <li class="parti_img">
                    <img :src="ovtrn.img_url" alt="">
                </li>
                <li class="parti_price">￥{{ovtrn.set_meal_price}} &nbsp;&nbsp;<span
                        class="parti_dr">原价<s>￥{{ovtrn.set_meal_originalprice}}</s></span></li>
                <li class="parti_introduce">{{ovtrn.set_meal_effect}}</li>
            </ul>

        </div>

    </div>
</template>
<script>
    import data from '../../../static/indexovr.json'
    export default {
        name: "particulars",
        data() {
            return {
                id: [],
                ovtrn: []
            }
        },
        mounted() {
            this.id = this.$route.query.id

            this.ovtrn = data.ovtrn[this.id];
            console.log(this.ovtrn)

        },
        created: function () {
            //     this.$axios.get("./static/indexovr.json", {}).then((res) => {
            //         console.log(res)
            //         this.dataumkk = res.data.ovtrn
            //         console.log(this.dataumkk)
            //     })
        },
    }
</script>
<style>
    .parti_ul {
        width: 100%;

    }

    .parti_img {
        width: 100%;

    }

    .parti_img img {
        width: 100%;
    }

    .parti_price {
        width: 100%;
        height: 30px;
        /* background: green; */
        line-height: 30px;
        font-size: 20px;
        font-weight: 600;
        color: red;
    }

    .parti_dr {
        font-size: 15px;
        color: #969696;
    }

    .parti_introduce {
        width: 100%;
        font-size: 22px;
        line-height: 32px;
        padding: 10px;
        box-sizing: border-box;
    }
</style>


